.expertise-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    overflow-x: hidden;
}

.expertise-content-info {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 25px;
    margin-bottom: 25px;
}

.expertise-content-info-dark {
    color: white;
}

.expertise-content-info-light {
    color: black;
}

/* Span styles common for both themes */
.expertise-content-info span:nth-child(1),
.expertise-content-info span:nth-child(2) {
    color: #5E5CE6; /* Common colour for spans 1 and 2 */
}

.expertise-content-info-dark span:nth-child(5),
.expertise-content-info-light span:nth-child(5) {
    color: var(--span-5-color);
}

.expertise-content-info-dark span:nth-child(6),
.expertise-content-info-light span:nth-child(6) {
    color: var(--span-6-color);
}

.expertise-content-info-dark span:nth-child(7),
.expertise-content-info-light span:nth-child(7) {
    color: var(--span-7-color);
}

/* Theme-specific colours */
:root {
    --span-5-color-dark: #FFD60A;
    --span-6-color-dark: #64D2FF;
    --span-7-color-dark: #FF375F;

    --span-5-color-light: #FFCC00;
    --span-6-color-light: #32ADE6;
    --span-7-color-light: #FF2D55;
}

.expertise-content-info-dark, .expertise-files-heading-dark {
    --span-5-color: var(--span-5-color-dark);
    --span-6-color: var(--span-6-color-dark);
    --span-7-color: var(--span-7-color-dark);
}

.expertise-content-info-light, .expertise-files-heading-light {
    --span-5-color: var(--span-5-color-light);
    --span-6-color: var(--span-6-color-light);
    --span-7-color: var(--span-7-color-light);
}

/* Divider styles */
.expertise-division {
    height: 65%;
    width: 1px;
}

.expertise-division-dark {
    background-color: #FFFFFF;
}

.expertise-division-light {
    background-color: #8E8E93;
}

/* Heading and content styles */
.expertise-heading {
    font-size: max(1.5vw, 18px);
}

.expertise-content {
    font-size: max(1.4vw, 18px);
    width: 50%;
}

.exepertise-content-files {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.expertise-files-container {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    margin-top: 15px;
    margin-bottom: 30px;
}

.expertise-files-heading {
    display: inline-block;
    width: auto;
    height: auto;
    padding: 7px 20px 7px 20px;
    border-radius: 50px;
    font-size: max(1.4vw, 18px);
    color: black;
    margin-left: 70px;
    margin-top: 10px;
}

/* Light mode styles */
.expertise-files-container-light:nth-of-type(1) .expertise-files-heading {
    background-color: #FFCC00; /* Example for Programming Languages in light mode */
}

.expertise-files-container-light:nth-of-type(2) .expertise-files-heading {
    background-color: #32ADE6; /* Example for Frameworks & Libraries in light mode */
}

.expertise-files-container-light:nth-of-type(3) .expertise-files-heading {
    background-color: #FF2D55; /* Example for Tools & Platforms in light mode */
}

/* Dark mode styles */
.expertise-files-container-dark:nth-of-type(1) .expertise-files-heading {
    background-color: #FFD60A; /* Example for Programming Languages in dark mode */
}

.expertise-files-container-dark:nth-of-type(2) .expertise-files-heading {
    background-color: #64D2FF; /* Example for Frameworks & Libraries in dark mode */
}

.expertise-files-container-dark:nth-of-type(3) .expertise-files-heading {
    background-color: #FF375F; /* Example for Tools & Platforms in dark mode */
}

.expertise-files {
    display: flex;
    flex-flow: wrap;
    width: 60%;
}

.expertise-credits {
    display: flex;
    justify-content: right;
    margin-right: 60px;
    margin-bottom: 30px;
}

.expertise-credits-dark {
    color: white;
}

.expertise-credits-light {
    color: black;
}

.expertise-credits a {
    color: inherit;
    text-decoration: none;
    font-weight: bold;
}

@media screen and (max-width: 1112px) {
    .expertise-content-info {
        flex-direction: column;
        gap: 30px;
    }

    .expertise-division {
        width: 65%;
        height: 1px;
    }

    .expertise-content {
        width: 70%;
    }

    .expertise-files-container {
        flex-direction: column;
    }

    .expertise-files-heading {
        margin: 10px 45px;
    }

    .expertise-files {
        width: calc(100% - 40px);
        justify-content: center;
        margin: 20px 20px 0px 20px;
    }

    .expertise-heading-container {
        display: flex;
        justify-content: center;
    }
}

