.contact-form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
}

.contact-header {
    display: flex;
    align-items: center;
    padding: 0 35px;
    width: calc(100% - 70px);
    height: 50px;
    font-size: max(18px, 1vw);
}

.contact-header-dark {
    background-color: #323234;
    color: white;
}

.contact-header-light {
    background-color: #EAEAEF;
    color: black;
}

.message-container {
    overflow-y: scroll;
    height: calc(100% - 275px);
    width: 100%;
}

.message-container-left {
    width: calc(100% - 70px);
    padding: 35px 35px 0 35px;
    display: flex;
    flex-direction: row;
    align-items: end;
    justify-content: start;
    gap: 35px;
}

.message-container-right {
    width: calc(100% - 70px);
    padding: 0 35px 25px 35px;
    display: flex;
    flex-direction: row;
    align-items: end;
    justify-content: end;
    gap: 35px;
}

.message-user-icon {
    height: 70px;
    width: 70px;
    border-radius: 100%;
    background-color: #C9FFCE;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 37px;
}

.message-user-icon:first-of-type {
    background-color: #FFC9FA;
}

.message-left {
    width: 55%;
    padding: 0 15px;
    border-radius: 15px 15px 15px 0;
    font-size: max(16px, 1.1vw);
}

.message-left-dark {
    background-color: #2C2C2E;
    color: white;
}

.message-left-light {
    background-color: #E5E5E5;
    color: black;
}

.message-right {
    width: auto;
    padding: 15px;
    border-radius: 15px 15px 0 15px;
    font-size: max(16px, 1.1vw);
    color: white;
}

.message-right-dark {
    background-color: #329AE6;
}

.message-right-light {
    background-color: #327AE6;
}

.contact-form {
    width: 80%;
    position: absolute;
    bottom: 20px;
    display: flex;
    flex-flow: row wrap;
    justify-content: start;
}

.message-sent {
    background-color: #2b2b2b;
    color: #32ade6;
    padding: 10px;
    border-radius: 8px;
    margin-bottom: 15px;
    text-align: center;
    font-weight: bold;
}

.form-input {
    width: 100%;
    padding: 15px;
    margin-bottom: 0px;
    border-radius: 0px;
    font-size: max(16px, 1vw);
    resize: none;
    font-family: arial;
    border: none;
}

.form-input-dark {
    background-color: #2b2b2b;
    color: #fff;
}

.form-input-light {
    background-color: #E5E5E5;
    color: black;
}

.input-top {
    border-radius: 10px 0 0 0;
    width: calc(50% - 60px);
}

.input-mid {
    border-radius: 0 10px 0 0;
    width: 50%;
}

.input-bot {
    border-radius: 0 0 10px 10px;
}

.form-input-dark::placeholder {
    color: #aaa;
}

.form-input-light::placeholder {
    color: #8E8E93;
}

.form-submit {
    background-color: #5e5ce6;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: max(16px, 1.1vw);
    transition: background-color 0.3s;
    margin-top: 10px;
}

.form-submit:hover {
    background-color: #3780EE;
}

.form-submit-dark {
    background-color: #329AE6;
}

.form-submit-light {
    background-color: #327AE6;
}