.help-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    overflow-x: hidden;
}

.help-container-dark {
    color: white;
}

.help-container-light {
    color: black;
}

.help-container-dark span {
    color: #5E5CE6;
}

.help-container-light span {
    color: #5856D6;
}

.help-content-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    font-size: max(1.4vw, 18px);
    align-items: center;
    margin-top: 40px;
    margin-bottom: 30px;
}


.help-divider {
    height: 65%;
    width: 1px;
}

.help-divider-dark {
    background-color: #FFFFFF;
}

.help-divider-light {
    background-color: #8E8E93;
}

.help-content {
    width: 40%;
}

.help-guide-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.help-table {
    width: 80%;
    border-collapse: collapse;
    font-size: max(1.4vw, 18px);
}

.help-table td,
.help-table th {
    text-align: left;
    vertical-align: top;
    padding: 0px 30px 45px 30px;
}


.help-table td:first-child {
    font-weight: bold;
    width: 35%;
}

.table-header {
    display: inline-block;
    padding: 7px 20px 7px 20px;
    margin-right: 100px;
    border-radius: 50px;
    font-size: max(1.4vw, 18px);
    color: black;
    font-weight: 600;
    background-color: #32ADE6;
}

@media screen and (max-width: 818px) {
    .help-table {
        table-layout: auto;
        width: 85%;
    }
    .help-table td:first-child {
        width: 15%;
    }

    .help-table th:first-of-type {
        padding: 0;

    }

    .table-header {
        margin-right: 0;
    }
}

@media screen and (max-width: 690px) {
    .help-content-container {
        flex-direction: column;
        gap: 20px;
    }

    .help-content {
        width: 70%;
    }

    .help-divider {
        height: 1px;
        width: 65%;
    }
}