.about-me-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 100%;
}

/* Theme-based container styling */
.about-me-container-dark {
    color: #fff;
}

.about-me-container-dark span {
    color: #5E5CE6;
}

.about-me-container-light {
    color: black;
}

.about-me-container-light span {
    color: #5856D6;
}

/* Heading styles */
.about-me-heading {
    font-size: max(1.5vw, 18px);
}

/* Divider styles */
.about-me-division {
    height: 60%;
    width: 1px;
}

.about-me-division-dark {
    background-color: #FFFFFF;
}

.about-me-division-light {
    background-color: #8E8E93;
}

/* Content styles */
.about-me-content {
    width: 40%;
    font-size: max(1.4vw, 18px);
}

@media screen and (max-width: 690px) {
    .about-me-container {
        flex-direction: column;
        justify-content: center;
        gap: 30px;
    }

    .about-me-division {
        width: 60%;
        height: 1px;
    }

    .about-me-content {
        width: 70%;
    }
}
