.file {
    margin: 0;
    text-align: center;
    cursor: pointer;
    border-radius: 15px;
    padding-right: 40px;
    padding-left: 10px;
    padding-bottom: 30px;
}

.file img {
    width: 85px;
    height: 85px;
    margin-bottom: 0px;
}

.file p {
    font-size: 16px;
    margin: 0;
    font-weight: bold;
    margin-bottom: 5px;
    margin-top: 5px;
}

.name-dark {
    color: white;
}

.name-light {
    color: black;
}

@media screen and (max-width: 1112px) {
    .file img {
        width: 70px;
        height: 70px;
    }
}

@media screen and (max-width: 428px) {
    .file {
        padding-right: 20px;
    }
}